import Vue from 'vue'
import VueI18n from "vue-i18n"
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from "../views/Login.vue";
import CarMenu from "../views/CarMenu";
import OrderDetail from "../views/OrderDetail";
import Success from "../views/Success";
import UserInfo from "../views/UserInfo";
import UserOrder from "../views/UserOrder";
import Register from "../views/Register";
import AddCar from "../views/admin/AddCar";
import AdminEdit from "../views/admin/AdminEdit";
import CarManage from "../views/admin/CarManage";
import CarEdit from "../views/admin/CarEdit";
import Index from "../views/admin/Index";
import OrderAll from "../views/admin/OrderAll";
import OrderColumnar from "../views/admin/OrderColumnar";
import OrderExamine from "../views/admin/OrderExamine";
import OrderSector from "../views/admin/OrderSector";
import ReturnCar from "../views/admin/ReturnCar";
import ReturnExamine from "../views/admin/ReturnExamine";
import UserAdd from "../views/admin/UserAdd";
import UserInfoAll from "../views/admin/UserInfoAll";
import UserEdit from "../views/admin/UserEdit";
import ErrorInfo from "../views/error/ErrorInfo";
import SIdentify from "../utils/SIdentify";

Vue.use(VueRouter)
Vue.use(VueI18n)

const routes = [
    {
        path: '/home',
        name: 'Home',
        component: Home
    },
    {
        path: "/",
        name: "",
        component: Login
    },
    {
        path: "/register",
        name: "",
        component: Register
    },
    {
        path: "/userInfo",
        name: "",
        component: UserInfo
    },
    {
        path: "/userOrder",
        name: "",
        component: UserOrder
    },
    {
        path: "/success",
        name: "",
        component: Success
    },
    {
        path: "/carMenu",
        name: "",
        component: CarMenu
    },
    {
        path: "/orderDetail",
        name: "",
        component: OrderDetail
    },

    {
        path: '/adminMenu',
        name: '',
        iconName: 'el-icon-s-tools',
        component: Index,
        redirect: '/carManage',
        show: true,
        children: [
            {
                path: '/carManage', name: '', component: CarManage
            },
            {
                path: '/addCar', name: '', component: AddCar
            }
        ],

    },
    {
        path: "/carEdit",
        name: "",
        component: CarEdit,
        show: false
    },
    {
        path: '/daoHang2',
        name: '',
        iconName: 'el-icon-s-order',
        component: Index,
        show: true,
        children: [
            {
                path: '/orderExamine', name: '', component: OrderExamine
            },
            {
                path: '/orderAll', name: '', component: OrderAll
            }
        ]
    },
    {
        path: '/daoHang3',
        name: '',
        iconName: 'el-icon-back',
        component: Index,
        show: true,
        children: [
            {
                path: '/returnExamine', name: '', component: ReturnExamine
            },
            {
                path: '/returnCar', name: '', component: ReturnCar
            }

        ]
    },
    {
        path: '/daoHang4',
        name: '',
        iconName: 'el-icon-user-solid',
        component: Index,
        show: true,
        children: [
            {
                path: '/userInfoAll', name: '', component: UserInfoAll
            },
            {
                path: '/userAdd', name: '', component: UserAdd
            },
        ]
    },
    {
        path: "/userEdit",
        name: "",
        component: UserEdit,
        show: false
    },
    {
        path: "/adminEdit",
        name: "",
        component: AdminEdit,
        show: false
    },
    {
        path: '/daoHang5',
        name: '',
        iconName: 'el-icon-s-data',
        component: Index,
        show: true,
        children: [
            {
                path: '/orderSector', name: '', component: OrderSector
            },
            {
                path: '/orderColumnar', name: '', component: OrderColumnar
            },
        ]
    },
    {
        path: "/errorInfo",
        name:'',
        component: ErrorInfo,
    }
]

const router = new VueRouter({
    mode: 'history',
    base: "/carvue/",
    routes
})

router.beforeEach((to, from, next) => {
    if (to.matched.length === 0) { //
        from.name ? next({
            name: from.name
        }) : next('/errorInfo'); //404
    } else {
        next(); //
    }
});

export default router
