<template>
    <div class="userOrder_main" v-title data-title="" >
        <div class="userOrder">
            <el-page-header @back="goBack" content=""
                            style="color:deepskyblue;">
            </el-page-header>

            <div style="width:200px;margin-left:1170px">
                <el-button @click="downloadExcel()" type="primary"></el-button>
            </div>
            <el-table
                    :data="tableData"
                    border
                    style="width: 100%; margin-top:20px;text-align: center"
            >
                <el-table-column
                        prop="orderId"
                        label=""
                        width="220">
                </el-table-column>
                <el-table-column
                        prop="username"
                        label=""
                        width="90">
                </el-table-column>
                <el-table-column
                        prop="carName"
                        label=""
                        width="120">
                </el-table-column>
                <el-table-column
                        prop="carPrice"
                        label=""
                        width="80">
                </el-table-column>
                <el-table-column
                        prop="days"
                        label=""
                        width="60">
                </el-table-column>
                <el-table-column
                        prop="totalPrice"
                        label=""
                        width="120">
                </el-table-column>
                <el-table-column
                        prop="userAddress"
                        label=""
                        width="120">
                </el-table-column>
                <el-table-column
                        prop="userPhone"
                        label=""
                        width="120">
                </el-table-column>
                <el-table-column
                        prop="createTime"
                        label=""
                        width="103">
                </el-table-column>
                <el-table-column
                        prop="updateTime"
                        label=""
                        width="103">
                </el-table-column>
                <el-table-column
                        prop="state"
                        label=""
                        width="143"
                >
                </el-table-column>
            </el-table>

            <el-pagination
                    background
                    layout="prev, pager, next"
                    page-size="8"
                    :total="total"
                    style="margin-top: 10px"
                    @current-change="page">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    export default {
        name: "UserOrder",
        data() {
            return {
                tableData: '',
                total: '',
                value: '',
                user: JSON.parse(sessionStorage.getItem("user"))
            }
        },
        created() {
            if (this.user === null) {
                this.$router.push("/")
            }
            const _this = this;
            let id = this.$route.query.id;
            axios.get("http://39.106.154.21:8181/order/findOrderById/1/8/" + id).then(function (resp) {
                let data = resp.data.data
                _this.tableData = data.orderVO
                _this.total = data.count
            })

        },
        methods: {
            page(currentPage) {
                const _this = this
                let id = this.$route.query.id;
                axios.get('http://39.106.154.21:8181/order/findOrderById/' + currentPage + '/8/' + id).then(function (resp) {
                    let data = resp.data.data
                    _this.tableData = data.orderVO
                    _this.total = data.count
                })
            },
            goBack() {
                history.go(-1);
            },
            //
            downloadExcel() {
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.excelData = this.tableData //list
                    this.export2Excel()
                }).catch(() => {

                });
            },
            //excel
            export2Excel() {
                let _this = this;
                let user = JSON.parse(sessionStorage.getItem("user"))
                axios.get("http://39.106.154.21:8181/order/findAllByUserId/" + user.id).then(function (resp) {
                    _this.tableData = resp.data.data
                })
                require.ensure([], () => {
                    //@/+export2Excel
                    const {export_json_to_excel} = require('@/excel/export2Excel');
                    // 
                    const tHeader = ['', '', '', '', '', '', '', '', '', '', ''];
                    // 
                    const filterVal = ['orderId', 'username', 'carName', 'carPrice', 'days', 'totalPrice', 'userAddress', 'userPhone', 'createTime', 'updateTime', 'state'];
                    const list = _this.excelData;
                    const data = _this.formatJson(filterVal, list);
                    let username = user.username
                    // 
                    export_json_to_excel(tHeader, data, username + '');
                })
            },
            //
            formatJson(filterVal, jsonData) {
                return jsonData.map(v => filterVal.map(j => v[j]))
            },
        }
    }
</script>

<style scoped>
    .userOrder_main{
        padding:0px;margin: 0px;
       background:url("../../public/static/images/menu-bg.jpg") no-repeat fixed;
        background-size: 100% 100%;
    }
    .userOrder {
        width: 1280px;
        height: 700px;
        margin: 0 auto;
        padding: 10px;
    }


</style>