<template>
  <router-view v-if="isRouterAlive"></router-view>
</template>

<script>
import HelloWorld from './components/HelloWorld.vue'

export default {
  name: 'app',
  components: {
    HelloWorld
  },
  provide () {    //provideinject
    return {
      reload: this.reload
    }
  },
  data() {
    return{
      isRouterAlive: true                    //
    }
  },
  methods: {
    reload () {
      this.isRouterAlive = false;            //
      this.$nextTick(function () {
        this.isRouterAlive = true;         //
      })
    }
  },
}
</script>

