<template>
    <div v-title data-title="" >
        <el-page-header @back="goBack" content="" style="color:deepskyblue;margin-left: 200px;margin-top: 10px"/>
        <el-form class="demo-adminForm" ref="adminForm" :model="adminForm" :rules="rules" status-icon
                 label-width="100px">
            <el-form-item label="" prsop="id" style="width: 200px;">
                <el-input v-model="adminForm.id" style="color: #42b983" readonly></el-input>
            </el-form-item>

            <el-form-item label="" prop="username" style="width: 500px;">
                <el-input @blur="newAdminName" v-model="adminForm.username" autocomplete="off" :disabled=disabled
                          style="color: #42b983"></el-input>
            </el-form-item>

            <el-form-item label="" prop="src">
                <el-upload
                        class="avatar-uploader"
                        action="http://39.106.154.21:8181/account/uploadHeadImg"
                        :show-file-list="true"
                        accept=".jpg,.png"
                        :on-success="handleAvatarSuccess"
                        :before-upload="beforeAvatarUpload"
                >
                    <img v-if="imageUrl" :src="imageUrl" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </el-form-item>

            <el-form-item label="" prop="password" style="width: 500px;">
                <el-input type="password" v-model="adminForm.password" autocomplete="off"
                          :clearable="true" :disabled=disabled></el-input>
            </el-form-item>

            <el-form-item label="" prop="checkPassword" style="width: 500px;">
                <el-input type="password" v-model="adminForm.checkPassword" autocomplete="off"
                          :clearable="true" :disabled=disabled></el-input>
            </el-form-item>

            <el-form-item label="" prop="age">
                <el-input v-model.number="adminForm.age" :disabled=disabled></el-input>
            </el-form-item>

            <el-form-item label="" prop="gender" style="width: 400px;">
                <el-radio-group v-model="adminForm.gender">
                    <el-radio label="1"></el-radio>
                    <el-radio label="0"></el-radio>
                </el-radio-group>
            </el-form-item>

            <el-form-item label="" prop="address">
                <el-input v-model.number="adminForm.address" :disabled=disabled></el-input>
            </el-form-item>

            <el-form-item label="" prop="userPhone">
                <el-input v-model.number="adminForm.userPhone" :disabled=disabled></el-input>
            </el-form-item>

            <el-form-item label="" prop="likeModel">
                <el-input v-model.number="adminForm.likeModel" :disabled=disabled></el-input>
            </el-form-item>

            <el-form-item>
                <el-button type="primary" @click="submitForm('adminForm')"></el-button>
                <el-button @click="goBack"></el-button>
            </el-form-item>
        </el-form>

    </div>
</template>

<script>
    export default {
        name: "adminInfo",
        data() {
            //
            var pattern = new RegExp(
                "[`~!@#$^&*()=|{}':;',\\[\\].<>/?~@#&*|{}']"
            );
            var checkUsername = (rule, value, callback) => {
                if (!value) {
                    return callback(new Error(''));
                } else if (value.length < 3) {
                    callback(new Error('3'));
                } else if (pattern.test(this.adminForm.username)) {
                    callback(new Error(""));
                } else {
                    callback();
                }
            }

            //
            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if (value.length < 6) {
                    callback(new Error('6'));
                } else {
                    callback();
                }
            };

            //
            var validatePass2 = (rule, value, callback) => {
                if (this.adminForm.checkPassword.length < 6) {
                    callback(new Error('6'));
                } else if (this.adminForm.checkPassword !== this.adminForm.password) {
                    callback(new Error('!'));
                } else {
                    callback();
                }
            };

            //
            var checkAge = (rule, value, callback) => {
                if (!value) {
                    return callback(new Error(''));
                }
                setTimeout(() => {
                    if (!Number.isInteger(value)) {
                        callback(new Error(''));
                    } else {
                        if (value < 18) {
                            callback(new Error('18'));
                        } else {
                            callback();
                        }
                    }
                }, 500);
            };
            //
            var validateNum = (rule, value, callback) => {
                var re = /^1\d{10}$/
                // if (!re.test(value)) {
                //     callback(new Error(''))
                // }
                if (!/^[0-9]+$/.test(this.adminForm.userPhone) || !re.test(value)) {
                    callback(new Error(''))
                } else {
                    callback();
                }

            }

            //
            var address = (rule, value, callback) => {
                if (!value) {
                    return callback(new Error(''));
                } else {
                    callback();
                }
            }

            //
            var likeModel = (rule, value, callback) => {
                if (!value) {
                    return callback(new Error(''));
                } else {
                    callback();
                }
            }

            return {
                adminForm: {
                    id: '',
                    username: '',
                    password: '',
                    checkPassword: '',
                    age:'',
                    gender:'',
                    address:'',
                    userPhone:'',
                    likeModel:'',
                },
                rules: {
                    username: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: checkUsername, trigger: 'blur'}
                    ],
                    password: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: validatePass, trigger: 'blur'}
                    ],
                    checkPassword: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: validatePass2, trigger: 'blur'}
                    ],
                    age: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: checkAge, trigger: 'blur'}
                    ],
                    gender: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                    address: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: address, trigger: 'blur'}
                    ],
                    userPhone: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: validateNum, trigger: 'blur'}
                    ],
                    likeModel: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: likeModel, trigger: 'blur'}
                    ]
                },
                imageUrl: '',
                src: '',
                srcListL:'',
            }
        },
        created() {
            const _this = this;
            let id = JSON.parse(sessionStorage.getItem("admin")).id;
            axios.get("http://39.106.154.21:8181/account/getUser/" + id).then(function (resp) {
                _this.adminForm = resp.data.data
                _this.src = resp.data.data.src
                _this.srcList=resp.data.data.srcList
                _this.imageUrl = resp.data.data.src
            })
        },
        methods: {
            newAdminName() {
                const _this = this;
                axios.get("http://39.106.154.21:8181/account/newName/" + this.adminForm.username).then(function (resp) {
                    let login = resp.data.data
                    if (login.id!=null) {
                        let message = ''
                        _this.disabled = true
                            _this.$alert(message + ",", {
                                confirmButtonText: '',
                                callback: action => {
                                    _this.$router.go(0)
                                }
                            });
                    }
                })
            },
            handleAvatarSuccess(res, file) {
                this.imageUrl = URL.createObjectURL(file.raw);

                if(res.code===0){
                    this.$message.success("")
                    console.log(res.data)
                }
                else{
                    this.$message.error("")
                }
                this.src =  res.data
                this.srcList=res.data
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isLt2M = file.size / 1024 / 1024 < 2;

                if (!isJPG) {
                    this.$message.error(' JPG !');
                }
                if (!isLt2M) {
                    this.$message.error(' 2MB!');
                }
                return isJPG && isLt2M;
            },
            submitForm(formName) {
                let user = {
                    id: this.adminForm.id,
                    username: this.adminForm.username,
                    password: this.adminForm.password,
                    checkPassword: this.adminForm.checkPassword,
                    src: this.src,
                    srcList :this.srcList,
                    age: this.adminForm.age,
                    gender: this.adminForm.gender,
                    address: this.adminForm.address,
                    userPhone: this.adminForm.userPhone,
                    likeModel: this.adminForm.likeModel
                }
                const _this = this;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        axios.put("http://39.106.154.21:8181/account/adminEdit", user).then(function (resp) {
                            if (resp.data.code === 0) {
                                _this.$alert(resp.data.data, {
                                    confirmButtonText: '',
                                    callback: action => {
                                        _this.$router.push("/");
                                        sessionStorage.removeItem("admin")
                                    }
                                });
                            }else{
                                _this.$message.error("");
                            }
                        })
                    } else {
                        _this.$message.error("");
                        return false;
                    }
                });
            },
            goBack() {
                history.go(-1);
            },
        }
    }
</script>

<style>
    .demo-adminForm {
        border-radius: 15px;
        background-clip: padding-box;
        margin: 0px auto;
        width: 600px;
        padding: 35px 35px 15px 35px;
        background: #fff;
        border: 0px solid #eaeaea;
    }


</style>